body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-brandon-grotesque: "Brandon Grotesque";
  --font-titan-one: "Titan One";

  /* font sizes */
  --font-size-4xl: 23px;
  --font-size-18xl: 37px;
  --font-size-6xl: 25px;
  --font-size-51xl: 70px;
  --font-size-16xl: 35px;
  --font-size-xl: 20px;
  --font-size-11xl: 30px;
  --font-size-41xl: 60px;
  --font-size-31xl: 50px;
  --font-size-81xl: 100px;
  --font-size-8xl: 27px;
  --font-size-21xl: 40px;
  --font-size-22xl: 41px;
  --font-size-3xl: 22px;
  --font-size-base: 16px;
  --font-size-mid: 17px;
  --font-size-5xl: 24px;
  --font-size-4xs: 9px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #211d1f;
  --color-gray-200: #140902;
  --color-gray-300: rgba(20, 9, 2, 0.48);
  --color-orangered: #fe6227;
  --color-palevioletred: #f0709e;
  --color-goldenrod-100: #feba2f;
  --color-goldenrod-200: #fdb92f;
  --color-turquoise: #03c6b6;
  --color-seashell: #fff9f3;
  --color-black: #000;

  /* Border radiuses */
  --br-33xl: 52px;
  --br-28xl: 47px;
  --br-12xl: 31px;
  --br-6xs: 7px;
  --br-17xl: 36px;
  --br-19xl: 38px;
  --br-13xl: 32px;
  --br-4xs: 9px;
  --br-14xl: 33px;
  --br-43xl: 62px;
  --br-10xl: 29px;
}
